.filming-section{
  padding: get-m-vw(20px);

  background-color: $sand;
  background-image: url('~assets/images/Pattern_1.svg');
  color: $black;

  & > h2{ @include m-bebas-30-bold; }
  & > p {
    margin-top: get-m-vw(5px);
    @include m-helvetica-12-bold;
  }
}

.filming-section_projects{
  width: 100%;
  display: grid;
  grid-auto-rows: max-content;
  grid-row-gap: get-m-vw(20px);

  &_project{
    width: 100%;
    margin-top: get-m-vw(18px);
    display: grid;
    grid-template:
    repeat(4, max-content)/
    max-content 1fr;
    grid-column-gap: get-m-vw(10px);

    img{
      grid-row: 1/3;
      width: get-m-vw(90px);
      height: get-m-vw(128px);
      display: block;
    }

    h3{
      padding-top: get-m-vw(2px);
      @include m-bebas-20-bold;
    }

    ul{
      @include m-helvetica-12-bold;
      margin-top: get-m-vw(5px);
      display: grid;
      grid-auto-rows: max-content;
      grid-row-gap: get-m-vw(4px);

      span{ color: rgba($black, 0.7); }
    }

    p{
      grid-column: 1/3;
      margin-top: get-m-vw(18px);
      @include m-helvetica-12-bold;
    }

    a{
      grid-column: 1/3;
      width: max-content;
      height: get-m-vw(22px);
      padding: 0 get-m-vw(10px);
      padding-top: get-m-vw(5px);
      margin-top: get-m-vw(8px);
      display: block;
      @include m-bebas-14-bold;
      background-color: $black;
      color: $sand;
    }
  }
}

.filming-section_table{
  width: 100%;
  margin-top: get-m-vw(90px);

  &_spoiler{
    width: 100%;
    height: max-content;
    margin-top: get-m-vw(-2px);
    border: get-m-vw(2px) solid $black;

    &_button{
      width: 100%;
      height: get-m-vw(36px);
      padding-top: get-m-vw(8px);
      padding-left: get-m-vw(16px);
      position: relative;
      display: block;
      background: $sand;
      color: $black;
      @include m-bebas-20-bold;
      border-bottom: get-m-vw(2px) solid $black;
      z-index: 1;
    }

    &_content{
      width: 100%;
      height: max-content;
      margin-top: get-m-vw(-2px);
      max-height: 0px;
      position: relative;
      overflow: hidden;
      transition: max-height 0.3s;
      background: $sand;
      z-index: 0;
    }

    input{
      width: 0; height: 0;
      -webkit-appearance: none;
      position: absolute;
    }

    input:checked ~ &_content{
      max-height: 1000px;
    }

    input:checked ~ label{
      background: $black;
      color: $sand;
    }
  }
}

.filming-section_table{

  &_reference{
    padding: get-m-vw(24px) get-m-vw(16px);

    h3{ @include m-bebas-30-bold; }

    &_movies{
      margin-top: get-m-vw(18px);
      display: grid;
      grid-auto-rows: max-content;
      grid-row-gap: get-m-vw(14px);

      span{ @include m-bebas-20-bold; }

      ul{
        margin-top: get-m-vw(5px);
        display: grid;
        grid-auto-flow: column;
        grid-auto-columns: get-m-vw(38px);
        justify-content: space-between;
        text-align: center;
        @include m-bebas-20-bold;
      }

      ul:nth-child(2){ color: rgba($black, 0.7); }
    }
  }

  &_tariff-plan{
    display: flex;

    &_left{
      width: 50%;
    }

    &_right{
      width: 50%;
    }
  }

  &_additional{
    padding: get-m-vw(24px) get-m-vw(16px);
    display: grid;
    grid-auto-rows: max-content;
    grid-row-gap: get-m-vw(20px);

    h2{ @include m-bebas-30-bold; }
    h3{ @include m-bebas-20-bold; }
    p{ @include m-helvetica-12-bold; span{ color: rgba($black, 0.75); } }
  }
}

.filming-section_table{
  .slider .wrapper{
    position: relative;
    overflow: hidden;
  }

  .slider .next{
    width: get-m-vw(16px);
    height: get-m-vw(16px);
    top: 0;
    left: 0;
    position: absolute;
    background: $black;
  }

  .slider .container{
    display: flex;
    &.transition{
      transition: 0.3s;
    }
  }

  .slider .slide{
    flex-shrink: 0;
    width: get-m-vw(166px);
  }
}

.filming-section_aside{ display: none; }
